import React from "react";
import "./Footer.css";

const Footer = () => {
  return (
    <footer className="footer-container">
      <div className="footerText">
        <a href="zurich.com">Version</a>
        <p class="copyright">
          &copy; {new Date().getFullYear()}
          <a href="zurich.com">Zurich</a>
        </p>
      </div>
    </footer>
  );
};

export default Footer;
